export default{
	data(){
		return{
			uploadimg:[
				{
					id:1,
					name:"上传行驶证正面",
					inport:"*",
					certi:''
				},
				{
					id:2,
					name:"上传行驶证反面",
					inport:"*",
					certi:''
				},
				{
					id:3,
					name:"私家车请上传身份证",
					inport:'',
					certi:''
				},
				{
					id:4,
					name:"公司车请上传营业执照",
					inport:'',
					certi:''
				}
			]
			// certificate:[]
		}
	},
	methods:{
		jump(e){
			uni.navigateTo({
				url:e
			});
		},
		upload(i){
			 var self = this
			console.log("13");
			uni.chooseImage({
			    count: 6, //默认9
			    sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
			    sourceType: ['album'], //从相册选择
			    success: function (res) {
			        console.log(JSON.stringify(res.tempFilePaths));
					console.log(res);
					let  paths = res.tempFilePaths[0];
					self.uploadimg[i].certi = paths;
					self.$set(self.uploadimg[i] , 'operate' , true);
					console.log(self.uploadimg[i])
			    }
			});
		},
		closeimg(i){
			var self = this;
			self.$set(self.uploadimg[i] , 'operate' , false);
			self.uploadimg[i].certi = '';
			console.log(self.uploadimg[i]);
		}
	},
	onShow() {
		this.$service.auth(this , this.$service.api.index.diyFields , 'GET' , {type:1} , function(self , res){
			console.log(res);
		})
	}
}
